import React, { useCallback, useEffect, useMemo, useState } from "react"
import { useTheme } from "@emotion/react"

// Layout component
import PageTransition from "@src/components/layout/PageTransition"
import Overlay from "@src/components/layout/Overlay"
import Loading from "@src/components/Loading"
import withLoading from "@src/hocs/withLoading"

const WrapPage = ({ location, loading, startLoading, children }) => {
  const theme = useTheme()
  /*const localizedPathName = useMemo(
    () =>
      location.pathname.replace(new RegExp(`^/${pageContext.locale}`, "g"), ""),
    [location.pathname, pageContext.locale]
  )*/

  // Transition
  const [pageTransition, setPageTransition] = useState(false)

  // Hook load asset before loading
  const loadBeforeLoading = useCallback(async () => {
    const { default: fontObserver } = await import(
      /* webpackChunkName: "fonts" */
      /* webpackMode: "lazy" */
      /* webpackPreload: true */
      "./fonts"
    )

    return Promise.all([
      fontObserver(theme.fonts.body, {
        weight: theme.fontWeights.body,
      }),
      fontObserver(theme.fonts.header, {
        weight: theme.fontWeights.header,
      }),
    ])
  }, [theme])

  // OnMount
  useEffect(() => {
    loadBeforeLoading().finally(() => startLoading())
  }, [loadBeforeLoading, startLoading])

  return (
    <>
      {/*<Overlay active={pageTransition} />*/}
      <PageTransition
        pathname={loading ? location.pathname : "loading"}
        OnStart={() => setPageTransition(true)}
        OnFinish={() => setPageTransition(false)}
      >
        {children}
      </PageTransition>
    </>
  )
}

export default withLoading(WrapPage, Loading)
