import styled from "@emotion/styled"
import React from "react"
import { CSSTransition } from "react-transition-group"
import PropTypes from "prop-types"
import { Helmet } from "react-helmet"

const Content = styled.p`
  opacity: 0;

  .loading-enter-active & {
    opacity: 1;
    transition: opacity 400ms;
  }
  .loading-enter-done & {
    opacity: 1;
  }

  .loading-exit & {
    opacity: 1;
  }
  .loading-exit-active & {
    opacity: 0;
    transition: opacity 1000ms;
  }
`

const Wrapper = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  color: white;
  background: black;

  z-index: ${props => props.theme.zIndices.loading};

  &.loading-exit-active {
    opacity: 0;
    transition: opacity 100ms ease-out;
  }

  &.loading-exit-done {
    display: none;
  }

  .no-js & {
    display: none;
  }
`

const Loading = ({ active, stopLoading }) => (
  <>
    <Helmet>
      <noscript>{`<style>${Wrapper} {display: none;}</style>`}</noscript>
    </Helmet>
    <CSSTransition
      in={active}
      timeout={{
        enter: 1000,
        exit: 1000,
      }}
      classNames="loading"
      onEntered={stopLoading}
    >
      <Wrapper></Wrapper>
    </CSSTransition>
  </>
)

Loading.propTypes = {
  active: PropTypes.bool,
  stopLoading: PropTypes.func.isRequired,
}

export default Loading
